.container {
  display: flex;
}
.form {
  width: 600px;
  margin-bottom: 4rem;
  margin-right: 4rem;
}
.form__comment {
  display: flex;
  justify-content: space-between;
}
.form__inputName {
  width: 330px;
  height: 33px;
  padding: 10px 16px;
  margin-bottom: 20px;
  font-size: 14px;
  letter-spacing: 1.3px;
}
.form__inputText {
  width: 565px;
  padding: 18px 16px;
  font-size: 15px;
  line-height: 24px;
  letter-spacing: 0.9px;
  resize: none;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.form__inputName,
.form__inputText {
  border-radius: 8px;
  border: 1px solid #a7a7a7;
  outline: none;
  cursor: pointer;
  color: #202020;
  background: #fffbfb;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
  transition: transform 0.1s linear, box-shadow 0.15s linear;
}
.form__inputName:hover,
.form__inputText:hover {
  transform: scale(1.02);
  box-shadow: 0 5px 15px 0 rgb(255, 251, 251, 0.35);
}
.btn__submit {
  position: relative;
  display: block;
  width: 215px;
  height: 55px;
  padding: 0;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0.8px;
  border-radius: 5px;
  border: none;
  border: 1px solid #a7a7a7;
  outline: none;
  cursor: pointer;
  color: #000;
  background: #61dafb;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.45);
  transition: color 0.1s linear, letter-spacing 0.1s linear,
    transform 0.1s linear;
}
.btn__submit::before,
.btn__submit::after {
  position: absolute;
  content: "";
  z-index: -1;
  border-radius: 5px;
  background: #f30085;
}
.btn__submit:hover {
  border: none;
  color: #fff;
  letter-spacing: 1px;
  transform: scale(1.04);
}
.btn__submit::after {
  height: 0;
  width: 0;
  top: 50%;
  left: 50%;
  transition: all 0.2s;
}
.btn__submit:hover::after {
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
}
